@import url(https://fonts.googleapis.com/css2?family=Abril+Fatface&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-flex-direction:column;flex-direction:column}body{background:#281f35 url(/radio_api/static/media/radio-backgroundpiece.87c2d5f6.jpg);background:#281f35,#3d2e53;color:#f0f0f0;font-family:"Open Sans", cursive}h1{margin:1em 0 0 0;font-size:3rem}h2{margin-bottom:2em;font-size:2rem}.radio{max-width:60em;width:100%}.filters{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-flex-wrap:wrap;flex-wrap:wrap;margin-bottom:2em;font-size:1.2rem;width:100%}span{margin:0.25em;border:2px solid #e36bae;border-radius:10px;padding:0.25em 0.75em}span:hover{cursor:pointer;background:linear-gradient(#e66465, #9198e5)}.selected{background:linear-gradient(#e66465, #9198e5)}.stations{display:grid;grid-template-columns:repeat(3, 33%);width:100%}.station{font-size:1.2em;border:2px solid #4c3e5f;margin:0.25em;border-radius:10px;padding:1em;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.station:hover{border-color:#e66465}.stationName{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;margin-bottom:1em;width:100%;-webkit-align-items:center;align-items:center;-webkit-justify-content:start;justify-content:start}.name{width:100%;text-align:center}img{display:block;width:2.5em;height:2.5em;border-radius:50%;border:2px solid #4c3e5f}audio{width:100%}.rhap_container.player{background-color:#4c3e5f;display:-webkit-flex;display:flex;justify-items:center;padding:0,25em, 0,75em;border-radius:10px}.rhap_stacked .rhap_controls_section{margin_top:0 !important}.rhap_controls-section .rhap_volume-controls{-webkit-justify-content:center;justify-content:center}.rhap_controls-section .rhap_main-controls button[aria-label="Play"] svg path{fill:#ebebeb !important}.rhap_controls-section .rhap_main-controls button[aria-label="Pause"] svg path{fill:#e66465 !important}@media only screen and (max-width: 600px){.stations{grid-template-columns:repeat(1, 100%)}img{display:none}}

